    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    createOz(){
      const form = new FormData();
      for (var i=0;i<this.selected.length;i++){
        form.append(`subjects`, this.selected[i]);
      }
      form.append("banner_image", this.myImages[0]);
      form.append("cover_image", this.myImages1[0]);
      form.append("title", this.title);
      form.append("about", this.about);
      form.append("higher_education_info", this.higher_education_info);
      form.append("practice", this.practice);
      form.append("subject", this.subject);
      form.append("working_condition", this.working_condition);
      form.append("workplace", this.workplace);
      form.append("max_salary", this.max_salary);
      form.append("mid_salary", this.mid_salary);
      form.append("min_salary", this.min_salary);
      form.append("profession_type ", this.selected1);
      axios.post(`${baseUrl}professions/`,form,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'oz'
          }
        })
        .then(res=>{
          this.$toast("Ma'lumotlar O'zbek tillarda kiritildi", {
            timeout: 2000,
            type:"success"
          });
          form.append("title", this.titleru);
          form.append("about", this.aboutru);
          form.append("higher_education_info", this.higher_education_inforu);
          form.append("practice", this.practiceru);
          form.append("subject", this.subjectru);
          form.append("working_condition", this.working_conditionru);
          form.append("workplace", this.workplaceru);
          form.append("profession_name ", this.selected1);
          axios.put(`${baseUrl}professions/${res.data.id}/`,form,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language':'ru'
              }
            })
